<template>
  <div class="found">
    <div class="headTitle">修炼</div>
    <div class="topInfo">
      <div class="user">
        <span class="userName">Dear&ensp;{{ userInfo.fNickName }}</span>
        <span class="showInfo" @click="showPricticeDetails">
          <span>查看记录</span>
          <span class="next">
            <img src="../assets/img/next.png">
          </span>
        </span>
      </div>
    </div>
    <div class="pricticeInfo">
      <span class="days">
        <span class="num">0</span>
        <span class="time">天</span>
        <br>
        <span class="detail">累计修炼</span>
      </span>
      <span class="minutes">
        <span class="num">768</span>
        <span class="time">分钟</span>
        <br>
        <span class="detail">修炼总时长</span>
      </span>
    </div>
    <div class="border"></div>
    <div class="lessonPrictice">
      <div class="title">课程修炼</div>
      <div class="lessonInfo" :style="{ backgroundImage: 'url(' + backgroundImg + ')'}" @click="$router.push('/home/teachers')">
        <span class="add">
          <span class="icon">+</span>&ensp;添加修炼内容</span>
      </div>
    </div>
    <div class="border"></div>
    <div class="allLessons">
      <div class="title">全部课程</div>
      <div class="lessons">
        <div class="lessonInfo" @click="play(index)" :style="{ backgroundImage: 'url(' + lesson.backgroundImg + ')'}" v-for="(lesson, index) in lessons" :key="index">
          <div class="lessonDetails">
            <div class="lessonName">{{ lesson.title }}</div>
            <div class="action">解析与修炼</div>
            <div class="pricticeNum">已有0人加入此修炼</div>
          </div>
          <div class="mask"></div>
        </div>
      </div>
    </div>
    <BottomNav></BottomNav>
  </div>
</template>

<script>
import { getUsersInfo } from '../api/getdata'
import store from '../vuex/store'
import BottomNav from '../components/BottomNav'
export default {
  name: 'prictice',
  data () {
    return {
      userInfo: {},
      userId: '',
      backgroundImg: require('../assets/img/pricticeBackground.png'),
      lessons: [{
        url: 'http://47.102.101.78/images/assets/videos/staticPractice/哼哈调气生阳功.mp4', title: '哼哈调气养生功', backgroundImg: 'http://47.102.101.78/images/assets/videos/staticPractice/images/哼哈调气生阳功.jpg'
      }, {
        url: 'http://47.102.101.78/images/assets/videos/staticPractice/引腰护肾.mp4', title: '引腰护肾', backgroundImg: 'http://47.102.101.78/images/assets/videos/staticPractice/images/引腰护肾.jpg'
      }, {
        url: 'http://47.102.101.78/images/assets/videos/staticPractice/肩胛调理.mp4', title: '肩胛调理', backgroundImg: 'http://47.102.101.78/images/assets/videos/staticPractice/images/肩胛调理.jpg'
      }, {
        url: 'http://47.102.101.78/images/assets/videos/staticPractice/静心桩.mp4', title: '静心桩', backgroundImg: 'http://47.102.101.78/images/assets/videos/staticPractice/images/静心桩.jpg'
      }, {
        url: 'http://47.102.101.78/images/assets/videos/staticPractice/韦陀献杵调肩颈.mp4', title: '韦陀献杵调肩颈', backgroundImg: 'http://47.102.101.78/images/assets/videos/staticPractice/images/韦陀献杵调肩颈.jpg'
      }]
    }
  },
  mounted () {
    this.userId = localStorage.getItem('userId')
    this.getUserInfo()
    // this.getLocation()
  },
  methods: {
    showPricticeDetails () {
      this.$router.push('/home/prictice/pricticeDetails')
    },
    play (index) {
      store.commit('getVideoInfo', {fVideoUrl: this.lessons[index].url, fVideoCoverImgUrl: this.lessons[index].backgroundImg, fVideoName: this.lessons[index].title})
      this.$router.push('/home/mine/videoPlayer')
    },
    getUserInfo () {
      let data = getUsersInfo({fUserId: this.userId})
      data.then(res => {
        // console.log(res)
        this.userInfo = res.data
      })
    }
  },
  components: {
    BottomNav
  }
}
</script>

<style scoped>
 * {
   margin: 0;
   padding: 0;
   font-size: 0.48rem;
 }

  .headTitle {
    width: 100%;
    color: #333333;
    height: 1.17333rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    background: #fff;
    z-index: 99;
    top: 0;
    left: 0;
  }

  .topInfo {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 2rem;
  }

  .user {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .userName {
    font-size: 0.42667rem;
    color: #333333;
  }

  .showInfo {
    display: flex;
    align-items: center;
  }

  .showInfo span:first-child {
    font-size: 0.4rem;
    color: #666666;
  }

  .next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 0.21132rem;
    height: 0.4rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .showInfo span:first-child {
    margin-right: 0.21333rem;
    color: #666666;
  }

  .pricticeInfo {
    width: calc(100% - 4rem);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
  }

  .num {
    color: #ff8309;
    font-size: 0.8rem;
  }

  .time {
    color: #666666;
  }

  .detail {
    color: #333333;
  }

  .time,
  .detail {
    font-size: 0.42667rem;
  }

  .border {
    width: 100%;
    height: 0.32rem;
    background: #f5f5f5;
    margin-top: 0.52rem;
  }

  .allLessons,
  .lessonPrictice {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 0.37333rem;
  }

  .title {
    text-align: start;
  }

  .lessonInfo {
    width: 4.48rem;
    height: 3.2rem;
    background-size: 100% 100%;
    background-repeat:  no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0.26667rem;
    color: #fff;
    position: relative;
  }

  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .4;
  }

  .add {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.42667rem;
  }

  .icon {
    display: inline-block;
    margin-bottom: 0.15rem;
    font-size: 0.48rem;
  }

  .lessons {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 2rem;
  }

  .lessonDetails {
    width: calc(100% - 0.45rem);
    height: 100%;
    z-index: 2;
    font-weight: 600;
  }

  .lessonName {
    margin-top: 0.4rem;
    font-size: 0.42667rem;
  }

  .action {
    margin-top: 0.34667rem;
    font-size: 0.34667rem;
  }

  .pricticeNum {
    margin-top: 0.6rem;
    font-size: 0.34667rem;
  }

  .lessonName,
  .action,
  .pricticeNum {
    text-align: start;
  }
</style>



